* {
  margin: 0;
  padding: 0;
}

body {
  overflow-x: hidden;

  &::-webkit-scrollbar {
    display: none;
  }

  -ms-overflow-style: none;

  scroll-behavior: smooth;

}

#loading {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background: #01a2ff;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: Poppins, serif;
  font-size: 40px;
  color: #f0f3fb;
}

#introduction {
  position: relative;
  width: 100vw;
  padding: 339px 0 380px;
  background: url("../img/introduction_background.png");
  background-size: cover;

  .logo-wrapper {
    position: absolute;
    top: 50px;
    left: 50px;
    height: 31px;

    img {
      position: relative;
      height: 100%;
      background-size: cover;
    }

  }

  .us-wrapper {
    position: absolute;
    top: 62px;
    right: 62px;

    a {
      text-decoration: none;
      cursor: pointer;
      -webkit-tap-highlight-color: transparent;

      h4 {
        font-family: Poppins, serif;
        font-size: 25px;
        font-weight: normal;
        color: #2f2a2e;
      }

    }

  }

  .wrapper {
    position: relative;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;

    .title-wrapper {
      position: relative;
      left: 150px;

      h1 {
        position: relative;
        margin-bottom: 16px;
        font-family: Poppins, serif;
        font-size: 90px;
        font-weight: 500;
        text-align: left;
        color: #7C9FFF;
        line-height: 110px;
      }

      h4 {
        position: relative;
        margin-bottom: 24px;
        font-family: Poppins, serif;
        font-size: 40px;
        font-weight: 500;
        text-align: left;
        color: #33343B;
      }

      h5 {
        position: relative;
        font-family: Poppins, serif;
        font-size: 25px;
        font-weight: normal;
        text-align: left;
        color: #33343B;
      }

    }

  }

}

#study {
